import React, { Component } from 'react'
import { Menu, Icon, Layout } from 'antd'
import 'antd/dist/antd.css'

const { Header, Content, Footer } = Layout;
const { SubMenu } = Menu
const styleLeft = { float: "left" }
const styleRight = { float: "right" }
class Navbars extends Component {
  handClick = (e) => {
    console.log(e)
    window.location.href = e.key
  }
  render() {
    return (
      <div>
        <div style={{
          position: "absolute",
          textAlign: "center",
          width: "100%",
        }} >
          <img src={"https://seeklogo.com/images/N/next-js-logo-7929BCD36F-seeklogo.com.png"} width="7%" />
        </div>
        <div>
          <Menu mode="horizontal" onClick={this.handClick} style={{ borderColor: "#FFFFFF" }}  >
            <Menu.Item style={styleLeft} key={"/index"}>
              หน้าแรก
          </Menu.Item>
            <Menu.Item style={styleLeft} key={"/package"}>
              แพกเกจ
          </Menu.Item>
            <Menu.Item style={styleLeft} key={"/contact"}>
              ติดต่อเรา
          </Menu.Item>
            <Menu.Item style={styleRight} key={'/login'} >
              LOGIN <Icon type={"login"} />
            </Menu.Item>
            <SubMenu
              title={"เพิ่มรายการ"}
              style={styleRight}
            >
              <Menu.ItemGroup>
                <Menu.Item key="/cod_waiting">cod_waiting</Menu.Item>
                <Menu.Item key="/cod_paybak">cod_paybak</Menu.Item>
              </Menu.ItemGroup>
            </SubMenu>
          </Menu>
        </div>
      </div>
    )
  }
}
export default Navbars
